* {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

:focus {
  outline: none;
}

.cat-info {
  margin-top: 25px;
  display: flex;
}

.breed-select {
  width: 1025px;
  cursor: pointer;
}

.thumb {
  max-width: 600px;
  margin-left: 25px;
}

.thumb-pic {
  position: relative;
}

.thumb-pic:before {
  z-index: 2;
  content: "";
  width: 50%;
  height: 100%;
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .3) 100%);
  display: block;
  position: absolute;
  top: 0;
  left: -75%;
  transform: skewX(-25deg);
}

.thumb-pic:hover:before {
  animation: shine .75s;
}

@keyframes shine {
  100% {
    left: 125%;
  }
}

.option:hover {
  color: #fff;
  transition: all .3s;
}

.option:nth-child(6n+1):hover {
  background-color: #000;
}

.option:nth-child(6n+2):hover {
  background-color: #ea4c89;
}

.option:nth-child(6n+3):hover {
  background-color: #0057ff;
}

.option:nth-child(6n+4):hover {
  background-color: #32c766;
}

.option:nth-child(6n+5):hover {
  background-color: #f48024;
}

.option:nth-child(6n+6):hover {
  background-color: #006400;
}

